.contain {
  display: flex;
  height: 100%;
}
.menu {
  display: flex;
  background-color: rgb(181, 217, 253);
  flex-direction: column;
  margin: 0px 0.5rem 0px 0px;
  padding: 0px 5px;
  height: 100%;
}
.content {
  width: 100%;
  height: 100%;
}
.boxes {
  display: flex;
  gap: 4px;
  justify-content: space-between;
  align-items: center;
  background-color: var(--bs-primary);
  color: var(--bs-light);
  border-radius: 5px;
  margin: 0.4rem 0.2rem;
  padding: 0.3rem;
}
.rounded {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 30px;
  height: 30px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.rounded::-webkit-color-swatch {
  border-radius: 5px;
  border: 1px solid white;
}
.rounded::-moz-color-swatch {
  border-radius: 15px;
  border: 1px solid white;
}
