.main {
  width: 100%;
  height: 100%;
}
.row {
  height: 50%;
  margin: 5px 0px;
}
.col {
  height: 100%;
  padding: 0px 5px;
  margin: 0px;
}
.box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  background-color: black;
  color: white;
  border: 2px solid red;
  border-radius: 10px;
  padding: 10px;
}
.content {
  height: 90%;
  overflow-y: scroll;
  scrollbar-width: none;
}
.content pre code {
    color: #79c043;
  text-wrap: wrap;
}
.copy{
    text-align: right;
}
.copy img{
    width: 20px;
    cursor: pointer;
}
.theme {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  row-gap: 4px;
  column-gap: 4px !important;
}
.theme div {
  border: 1px solid black;
  padding: 10px;
  border-radius: 5px;
}
.theme div div {
  color: white;
  text-shadow: 1px 1px 2px black;
  background: rgba(37, 12, 12, 0.327);
  border-radius: 16px;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}
